.social-container {
    animation: slide-in 1.5s forwards;
    display: flex;
    flex-direction: column;
    gap: 2em;
    justify-content: center;
    position: sticky;
    top: 15%;
    float: left;
    height: 80vmin;
    width: 5%; 
    border-right: 1px solid rgba(158, 252, 181, 0.274);
    background-color:inherit; 
  }


  @media (max-width: 900px) {
    .social-container {
      animation: slide-in 1.5s forwards;
      display: flex;
      flex-direction: row;
      height: auto;
      width: 100%;
      top: unset;
      bottom: 0;
      float: none;
      border-right: none;
      border-top: 1px solid rgba(158, 252, 181, 0.274);
    }
  }